@import "../../Helper/variable";
@import "../../Helper/mixin";

.detail {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  background-blend-mode: multiply;
  background-color: rgba(0, 0, 0, 0.898);
  
}

.detail__content {
  .buy__ticket{
    font-size: 14px;
		color: #fb4226;
		border: 1px solid #fb4226;
		border-radius: 4px;
		text-align: center;
		background-color: transparent;
		padding: 7px 25px;
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
		margin: auto;
		margin-top: 35px;
    font-weight: 600;
		&:hover {
			background-color: #fb4226;
			border-color: #fb4226;
			color: #000 !important;
		}
  }
 
 
  .content__title{
    font-size: 20px;
    color: #fff;
    font-weight: 700;
    
  } 
  .content__detail{
    font-size: 15px;
    color: #adb5bd;
  }
}
