@import "../Helper/variable";
@import "../Helper/mixin";
#news {
	width: 80%;
	margin-left: 10%;
	max-height: 1150px;
	margin-bottom: 4rem;
	background-color: #fff;
	color: #000;
	.bg__top img{
		width: 100%;
		height: 100px;
		
	}
}
#myTab {
	border-bottom: none;
	width: 500px;
	height: 35px;
	margin: auto;
	.nav-item {
		.nav-link {
			background: none;
			border: none;
			font-size: 18px;
			line-height: 24px;
			height: 24px;
			color: #000;
			-webkit-transition: 0.2s all;
			transition: 0.2s all;
			font-weight: bold;
			&.active {
				background: none;
				border: none;
				font-size: 24px;
				line-height: 24px;
				height: 24px;
				color: #fb4226;
			}
			&:hover {
				font-size: 24px;
				line-height: 24px;
				height: 24px;
			}
		}
	}
}
.dienAnh24h__news .news__left,
.dienAnh24h__news .news__right {
	width: 403px;
	padding: 10px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
.dienAnh24h__news .news__left a,
.dienAnh24h__news .news__right a {
	font-size: 24px;
	text-decoration: none;
	color: #000;
	cursor: pointer;
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
}
.dienAnh24h__news .news__left a:hover,
.dienAnh24h__news .news__right a:hover {
	color: #fb4226;
}
.dienAnh24h__news .news__left a h4,
.dienAnh24h__news .news__right a h4 {
	overflow: hidden;
	text-overflow: ellipsis;
	line-height: 20px;
	height: 42px;
	font-size: 20px;
	-webkit-transition: #000 0.2s;
	transition: #000 0.2s;
	margin-top: 10px;
	margin-bottom: 5px;
  font-weight: 700;
}
.dienAnh24h__news .news__left p,
.dienAnh24h__news .news__right p {
	text-align: justify;
	font-size: 15px;
	color: #000;
	padding: 0 5px 0 0;
	line-height: 19px;
	letter-spacing: 0.17;
	overflow: hidden;
	text-overflow: ellipsis;
	height: 55px;
	margin-bottom: 15px;
}
.icon__like__comment {
	height: 25px;
	float: left;
	width: 100%;
}
.icon__like__comment .like,
.icon__like__comment .comment {
	width: 36px;
	height: 20px;
	margin-right: 25px;
	cursor: pointer;
}
.icon__like__comment .like img,
.icon__like__comment .comment img {
	display: table-cell;
	vertical-align: middle;
	margin-right: 10px;
	margin-top: 2px;
	height: auto;
	width: 18px;
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
	display: inline-block;
}
.icon__like__comment .like:hover img,
.icon__like__comment .comment:hover img {
	-webkit-transform: scale(1.2);
	transform: scale(1.2);
}
.icon__like__comment .like span,
.icon__like__comment .comment span {
  color: #000;
	font-size: 12px;
	line-height: 1;
}
.list__news {
	margin-top: 15px;
	.news {
		padding: 10px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
	.col-sm-4 {
		a {
			font-size: 24px;
			text-decoration: none;
      color: #000;
			cursor: pointer;
			-webkit-transition: all 0.2s;
			transition: all 0.2s;
			img {
				border-radius: 3px;
				margin-bottom: 5px;
			}
			&:hover {
				color: #fb4226;
			}
		}
		.title {
			h4 {
				overflow: hidden;
				text-overflow: ellipsis;
				line-height: 20px;
				height: 42px;
				font-size: 20px;
				-webkit-transition: #000 0.2s;
				transition: #000 0.2s;
				margin-bottom: 10px;
        font-weight: bold;
			}
		}
		.p__text {
			text-align: justify;
			font-size: 15px;
      color: #000;
			padding: 0 5px 0 0;
			line-height: 19px;
			letter-spacing: 0.17;
			overflow: hidden;
			text-overflow: ellipsis;
			height: 55px;
			margin-bottom: 15px;
		}
	}
}
.news__4 {
	margin-top: 10px !important;
	.col-sm-12 {
		margin-bottom: 15px;
	}
	img {
		height: 50px;
		width: 50px;
		float: left;
		margin-right: 10px;
		border-radius: 4px;
	}
	a {
		font-size: 24px;
		text-decoration: none;
    color: #000;
		cursor: pointer;
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
		&:hover {
			color: #fb4226;
		}
		h4 {
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
			line-height: 20px;
			font-family: "SF Medium";
			height: 42px;
			font-size: 16px;
			-webkit-transition: #000 0.2s;
			transition: #000 0.2s;
			margin-bottom: 5px;
      font-weight: bold;
		}
	}
}
.xemThem {
	button {
		font-size: 14px;
		color: #000;
		border: 1px solid #fb4226;
		border-radius: 4px;
		text-align: center;
		background: 0 0;
		padding: 7px 25px;
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
		margin: auto;
		margin-top: 35px;
		display: block;
    	font-weight: 600;
		cursor: pointer;
		&:hover {
			background-color: #fb4226;
			border-color: #fb4226;
			color: #000 !important;
		}
	}
}


