@include Responsive(sm){
	#news{
		ul{
			margin-left: 15%;
		}
	}
   
}
@include Responsive(xs){
    #news {
       max-width: 420px;
	   max-height: max-content;
	   width: 100%;
	   margin-left: 0;
	   ul{
		   max-width: 300px;
		   transform: translateX(-18px);
		   .nav-item{
			   padding: 0;
			   .nav-link.active{
				font-size: 15px;
				line-height: 15px;
				height: 15px;
			   }
			   .nav-link{
				   font-size: 13px;
				   line-height: 13px;
				   height: 13px;
			   }
		   }
	   }
	   .dienAnh24h__news,.list__news,.news__4{
		   h4{
			   font-size: 15px;
		   }
		   p{
			   font-size: 12px;
			   height: max-content;
		   }
		   .icon__like__comment{
			   .like, .comment{
				font-size: 10px;
				.number{
					font-size: 10px;
				}
			   }
		   }
	   }
    }
}