@import '../Helper/variable';

.img__carousel{
    cursor: pointer;
    margin-top: 64px;
    position: relative;
    .icon__video{
        background-color: transparent; 
        opacity: 0;
        z-index: 5;
        font-size: 5rem;
        color: #fb4226;
        position: absolute;
        right: 0;
        left: 47%;
        bottom: 70%;
        top: 30%;
        transition: all 0.5s;
    }
    &:hover{
        .icon__video{
            opacity: 0.75;
           
        }
    }  
}

.btn.focus, .btn:focus {
    box-shadow: none;
}