.booking__movie {
  background-color: #e8e8e8; 
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  // background-blend-mode: multiply;
  // background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100%;
 
}
#list__seat{
  
  .tenGhe{
    color: #000;
    // margin-left: 8px;
    font-size: 12px;
    margin: 0;
    padding: 0;
    margin-left: 3px;
  }
}
.loai__ghe{
  .col-3{
    color: #000;
  }
}
.booking__title {
  margin-top: 12%;
}
.info__title {
  margin-top: 20%;
  color: #000;
  .address {
    span{
      margin-right: 20px;
    }
  }
  .info__film {
    h5 {
      color: #fb4226;
      text-align: left;
      font-size: 40px;
      font-weight: 600;
      line-height: 40px;
    }
  }
  .btn__delete, .btn__delete__all{
    cursor: pointer;
    color: #fb4226;
    background-color: transparent;
    border-radius: 5px;
    // border: 1px solid #ad3613 ;
    transition: all 0.3s;
      &:hover{
          transform: scale(1.5);
      }
  }
  .table {
    border: 1px solid #dee2e6; 
    // background-color: #3e515d;
    thead {
      tr {
        th{
          color: #000;
          // font-weight: bold;
        }
      }
    }
    tbody{
      tr{
        td{
          color: #000;
        }
      }
    }
  }
  tfoot{
    th{
      color: #000;
    }
    .btn__booking{
      background-color: #fb4226;
      color: white;
      border-radius: 15px;
    }
  }
}

.ghe {
  // border: 5px solid #3e515d;
  width: 15px;
  height: 15px;
  // border-radius: 50%;
  cursor: pointer;
  // background-color: #3e515d;
  background-color: transparent;
  // padding-top: 10px;
  border:none;
  .ghe__icon{
    color: #3e515d;
  }
}
button:focus {
  outline: 0px dotted;
  outline: 0px auto -webkit-focus-ring-color;
}
.gheVip {
  // border: 5px solid #fb4226;
  width: 15px;
  height: 15px;
  
  background-color: transparent;
  // padding-top: 10px;
  border:none;
  // border-radius: 50%;
  cursor: pointer;
  // background-color: #fb4226;
  // padding-top: 10px;
  .ghe__icon{
    color: #fb4226;
  }
}
.gheDuocChon {
  // border: 5px solid #f7b500;
  // background-color: #f7b500;
  font-size: 10px;
  cursor: no-drop !important;
  // border-radius: 50%;
  width: 15px;
  height: 15px;
  
  background-color: transparent;
  border: none;
  // padding-top:10px;
  .ghe__icon{
    color: #f7b500;
  }
}

.gheDangChon {
  // border: 5px solid #44c020;
  background-color: transparent;
  border: none;
  // color: #000;
  cursor: pointer;
  
  // border-radius: 50%;
  width: 15px;
  height: 15px;
  font-size: 10px;
  // padding-top: 10px;
  .ghe__icon{
    color: #44c020;
  }
}




.booking__movie {
  @include Responsive(xl) {
    
    .row{
      margin-right: 0;
    }
  }
  @include Responsive(md) {
    height: auto;

   
    #responisve__booking {
      display: block;
    }
    .col-7,
    .col-5{
        max-width: none;
    }
  }
  @include Responsive(sm) {
    
    #responisve__booking {
      display: block;
    }
    .col-7,
    .col-5{
        max-width: none;
    }
  }
  @include Responsive(xs) {

    
    #responisve__booking {
      display: block;
    }
    .ghe,
    .gheVip,
    .gheDangChon,
    .gheDuocChon {
      width: 22px;
      height: 22px;
    }
    #list__seat {
     padding: 1px;
      width: 100%;
      display: inline;
    }
    .col-7,
    .col-5{
        max-width: none;
    }
    margin-top: 100px;
    height: auto;
  }
}
