@import '../../Helper/mixin';
@import '../../Helper/variable';


.wrapper a {
  text-decoration: none;
  color: unset;
}

.wrapper img {
  max-width: 100%;
}

.main-color {
  color: var(--main-color);
}

.container {
  max-width: 1920px;
  padding: 0 40px;
  margin: auto;
}

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  background-color: #000000;
  .dropdown-toggle::after{
    display: none;
  }
}
.wrapper .menu__right{
  position: relative;
  // width: 100%;
}
.wrapper .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--text-color);
  height: var(--nav-height);
}

.wrapper .nav a {
  color: var(--text-color);
  text-decoration: none;
}

.wrapper .logo {
  font-size: 2rem;
  font-weight: 900;
  padding: 0;
}

.wrapper .nav__menu {
  position: absolute;
  right: 0px;
  list-style-type: none;
  display: flex;
  align-items: center;
  padding: 0 20px;
  #dropdownMenuButton{
    i{
      height: 30px;
      width: 30px;
      border-radius: 50%;
      margin-right: 10px;
      padding-top: 5px;
      background-color: #fb4226;
    }
  }
}

.wrapper .nav__menu li ~ li {
  margin-left: 30px;
}

.wrapper .navbar-toggler span{
  background-color: #fff;
}

.wrapper .nav__menu a {
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
}

.wrapper .nav__menu a:hover {
  color: var(--main-color);
}

.wrapper .nav__menu a.btn:hover,
.wrapper a.logo:hover,
.wrapper a.movie-item:hover {
  color: unset;
}

.wrapper .btn {
  padding: 0.25rem 1.5rem;
  text-transform: uppercase;
  font-size: 1.25rem;
  font-weight: 700;
  display: inline-flex;
  position: relative;
  align-items: center;
}

.wrapper .btn-hover::before {
  z-index: 1;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background-color: var(--main-color);
  transition: 0.3s ease-in-out;
}

.wrapper .btn-hover:hover::before {
  width: 100%;
}

.wrapper .btn i,
.wrapper .btn span {
  z-index: 1;
}

.wrapper .btn i {
  margin-right: 1rem;
}

  .nav-link {
    justify-content:space-between;
    color: #000;
      transition: all 0.3s;
      font-size: 20px;
    
}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
  color: #fb4226;
}
  


