.btn__profile {
    cursor: pointer;
    color: #fff;
    background-color: #fb4226;
    border-radius: 5px;
    border: 1px solid #fb4226;
    transition: all 0.3s;
    &:hover{
        background-color: #ad3613;
        box-shadow: none
    }
    &:active{
        background: #fb4226;
        box-shadow: none
    }
    &:focus{
        background: #fb4226;
        box-shadow: none
    }
}
.title__profile {
    font-size: 15px;
    color:#fff
}





