@import '../../Helper/mixin';
@import '../../Helper/variable';

.hero-slide-item {
	padding-top: 45%;
	position: relative;
	overflow: hidden;
	img {
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
}
.hero-slide-item-content {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	// color: var(--text-color);
	display: flex;
	z-index: 5;
}


.hero-slide-item-content .btn {
	color: #ffffff;
	padding: 0.25rem 1.5rem;
	text-transform: uppercase;
	font-size: 1.25rem;
	font-weight: 700;
	display: inline-flex;
	position: relative;
	align-items: center;
  }
  
  .hero-slide-item-content .btn-hover::before {
	z-index: 1;
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background-color: var(--main-color);
	transition: 0.3s ease-in-out;
  }
  
  .hero-slide-item-content .btn-hover:hover::before {
	width: 100%;
  }
  
  .hero-slide-item-content .btn i,
  .hero-slide-item-content .btn span {
	z-index: 1;
  }
  
  .hero-slide-item-content .btn i {
	margin-right: 1rem;
  }




.item-content-wraper {
	padding-left: 5rem;
	width: 40%;
	background-color: rgba(0, 0, 0, 0.8);
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: relative;
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 100%;
		width: 50%;
		height: 100%;
		background: linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
	}
}
.item-content-title {
	font-size: 5rem;
	line-height: 5rem;
	font-weight: 900;
}
.movie-infos {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin-top: calc(var(--space-top) / 2);
}
.movie-info {
	display: flex;
	align-items: center;
	font-size: 0.8rem;
	font-weight: 600;
	span {
		margin-left: 5px;
	}
	i {
		color: var(--main-color);
	}
	~ {
		.movie-info {
			margin-left: 5px;
		}
	}
}
.item-content-description {
	font-size: 1.15rem;
	margin-top: var(--space-top);
	line-height: 1.5rem;
}
.item-action {
	margin-top: var(--space-top);
}
.arrow-right-slick {
	position: absolute;
	right: 0;
	color: #fff;
	top: 46%;
	background-color: transparent;
	font-size: 35px;
	transition: 0.5s all;
	cursor: pointer;
    z-index: 1;
	&:hover {
		color: #fb4226;
	}
}
.arrow-left-slick {
	position: absolute;
	left: 0;
	color: #fff;
	top: 46%;
	background-color: transparent;
	font-size: 35px;
	transition: 0.5s all;
	cursor: pointer;
    z-index: 1;
	&:hover {
		color: #fb4226;
	}
}
.hero-slide-item .overlay {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.5);
  }

  
