// List Cinemas

#cinema{
    width: 80%;
    margin-left: 10%;
}
#bg__top img {
    width: 100%;
    height: 100px;
}
#cinema__Content {
    .list__logo {
        display: block;
        min-height: 600px;
        border: 1px solid #ebebec;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        padding-left: 0px;
        padding-right: 0px;
        height: 600px;
        li {
            width: 100%;
            height: 90px;
            opacity: 0.5;
            cursor: pointer;
            a {
                width: 100%;
                height: 100%;
                position: relative;
                img {
                    justify-items: center;
                    text-align: center;
                    display: inline-block;
                    width: 50px;
                }
                &:after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 60%;
                    width: calc(100% - 30px);
                    transform: translateX(-50%);
                    border-bottom: 1px solid #fff;
                    border-color: rgba(238, 238, 238, 0.88);
                }
            }
            &:hover {
                opacity: 1;
            }       
        }
       
    }
    #address__cinema::-webkit-scrollbar {
        width: 2px;
        background-color: #fb4226;
    } 
    #address__cinema::-webkit-scrollbar-thumb {
        background-color: #fb4226;
    }
    #address__cinema::-webkit-scrollbar-track {
        // -webkit-box-shadow: inset 0 0 2px ;
        background-color: #fff;
    }
    
    #address__cinema {
        min-height: 600px;
        border-top: 1px solid #ebebec;
        border-bottom: 1px solid #ebebec;
        height: 600px;
        overflow-x: hidden;

        .cinemas {
            padding: 15px;
            padding-left: 20px;
            display: flex;
            clear: both;
            width: 100%;
            position: relative;
            opacity: 0.5;
            cursor: pointer;
            transition: all 0.2s;
            span {
                display: block;
                line-height: 1.4;
            }
            .cinema__name {
                font-size: 15px;
                color: #000;
                font-weight: 600;
            }
            .cinema__color {
                display: inline-block !important;
               
            }
           
            .info__movie {
                font-size: 11px;
                color: gray;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .movie__detail a {
                color: #fb4226;
                font-size: 12px;
            }
            .img__cinema {
                width: 50px;
                height: 50px;
                margin-right: 15px;
            }
        }
        .cinemas:hover {
            opacity: 1;
        }
        .cinemas::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            width: calc(100% - 40px);
            transform: translateX(-50%);
            border-bottom: 1px solid #fff;
            border-color: rgba(238, 238, 238, 0.88);
        }
    }













    .active {
        opacity: 1 !important;
    }

    #list__movie::-webkit-scrollbar {
        width: 2px;
        background-color: #fb4226;
    } 
    #list__movie::-webkit-scrollbar-thumb {
        background-color: #fb4226;
    }
    #list__movie::-webkit-scrollbar-track {
        background-color: #fff;
    }


    #list__movie {
        display: flexbox;
        height: 600px;   
        border: 1px solid #ebebec;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;
        .movie__time {
            display: flex;
            cursor: pointer;
            width: 100%;
        }
        .movie__item {
            display: flexbox;
            padding-bottom: 17px;
            padding-top: 20px;
            padding-right: 15px;
            padding-left: 20px;
            width: 100%;
            position: relative;
            margin-bottom: 0;
            border: none;
            box-shadow: none;
            -webkit-box-shadow: none;
        }
        .movie__item::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            width: calc(100% - 40px);
            transform: translateX(-50%);
            border-bottom: 1px solid #ccc;
            border-color: rgba(238, 238, 238, 0.88);
        }
        .img__movie {
            width: 50px;
            display: flex;
            height: 50px;
            object-fit: cover;
            margin-right: 20px;
        }
        .info__item {
            p {
                margin: 0;
                line-height: 1.7;
                font-size: 12px;
                color: #9b9b9b;
            }
           
            .movie__title {
                font-size: 25px;
                display: block;
                line-height: 25px;
                overflow: hidden;
                text-overflow: ellipsis;
                color: #fb4226;
                font-weight: 500;
            }
        }
        .movie__hours {
            display: flex;
            width: 100%;
            transition: height 0.3s ease-in-out;
        }
        .list__time {
            display: block;
            width: 100%;
            padding-top: 15px;
        }
        .type__theater {
            display: flex;
            width: 100%;
            // font-family: "SF Medium";
            font-size: 16px;
            color: #23b397;
            margin-bottom: 5px;
            font-weight: bold;
        }
        .btn__hours {
            width: 120px;
            // font-family: "SF Medium";
            font-size: 14px;
            font-weight: 400;
            margin: 0px 10px 10px 0px;
            padding: 5px 5px;
            transition: all 0.2s;
            text-decoration: none;
            text-align: center;
            background-color: rgba(246, 246, 246, 0.5);
            border-radius: 7px;
            color: #9b9b9b;
            border: 1px solid #e4e4e4;
            cursor: pointer;
            transition: all 0.5s;
            &:hover{
                background-color: #fb4226;
                
                color: #fff;
                border: 1px solid #fb4226;
            }
            // .movie-start-time {
            //     font-size: 18px;
            //     font-weight: 500;
            //     color: #108f3e;
            // }
            a {
                text-decoration: none;
                color: #000;
            }
        }
    }
}

// responsive
#cinema{
    @include Responsive(xl){
      display: block;
    }
    @include Responsive(md){
        display: none;
        
    }
    @include Responsive(sm){
        display: none;
      
    }
    @include Responsive(xs){
        display: none;
       
    }
}