@import "../Helper/variable";
@import "../Helper/mixin";


#showing__time {
  width: 100%;
  margin-bottom: 15px;
  border: none;
  .overlay {
    position: relative;
    transition: all 0.5s;
    overflow: hidden;
  }

  .overlay::after {
    content: "";
    position: absolute;
    display: block;
    width: 0%;
    height: 330px;
    background: linear-gradient(to right, #00000054, #0000003b);
    border-radius: 5px;
    top: 0;
    left: -50%;
    transform: skewX(180deg);
    transition: all 0.5s;
  }
  .overlay:hover::after {
    width: 200%;
  }
  .overlay {
    img {
      border: none;
      vertical-align: top;
      width: 100%;
      height: auto;
      border-radius: 5px;
      transition: all 0.5s;
      z-index: 2;
    }
    &:hover {
      .btn__buy__ticket {
        visibility: visible;
        overflow: hidden;
        z-index: 3;
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fb4226;
        transition: 0.3s ease-in-out;
      }
      .title__movie {
        visibility: hidden;
        overflow: hidden;
      }
    }
  }

  .overlay {
    .btn__play__video {
      position: absolute;
      top: 40%;
      left: 35%;
      right: auto;
      bottom: auto;
      align-items: center;
      z-index: 1;
      background-color: #00000000;
      color: #fb4226;
      opacity: 0;
      transition: 0.5s;
    }
    i {
      font-size: 3rem;
    }
  }
  .overlay:hover .btn__play__video {
    opacity: 1;
  }
  .title__movie {
    position: relative;
    background-color: #fff;
    color: #000;
    transition: all 0;
    height: 60px;
    text-align: left;
    .btn__buy__ticket {
      // padding-top: 15px;
      font-size: 20px;
      width: 100%;
      // height: 70px;
      position: absolute;
      top: 0;
      color: #ffffff;
      // bottom: -10px;
      right: 0;
      left: 0;
      margin-top: 10px;
      z-index: 3;
      visibility: hidden;
      border: none;
      transition: all 0.1s;
      text-transform: uppercase;    
      font-weight: 700;   
      align-items: center;
    }
      p{
        color: #fb4226;
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 0;
      }
  }
}

#movie__schedule{
  width: 80%;
  margin-left: 10%; 
}
#movie__schedule .arrow-left-slick i {
  color: #000;
}
#movie__schedule .arrow-right-slick i {
  color: #000;
}
#movie__schedule .arrow-right-slick {
	position: absolute;
	right: -30px;
	color: #fff;
	top: 43%;
	background-color: transparent;
	font-size: 35px;
	transition: 0.5s all;
	cursor: pointer;
    z-index: 1;
	&:hover {
		color: #fb4226;
	}
}
#movie__schedule .arrow-left-slick {
	position: absolute;
	left: -30px;
	color: #fff;
	top: 43%;
	background-color: transparent;
	font-size: 35px;
	transition: 0.5s all;
	cursor: pointer;
    z-index: 1;
	&:hover {
		color: #fb4226;
	}
}

//responsive
#showing__time {
  @include Responsive(xl) {
    .overlay {
      width: 80%;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  @include Responsive(md) {
    width: 250;
    height: 200;
  }
  @include Responsive(sm) {
    margin: 5px;
    padding: 10px;
    width: 150;
    height: 200;
  }
  @include Responsive(xs) {
    margin: 5px;
    padding: 10px;
    width: 150;
    height: 200;
  }
}


