#tix__signin {
  background-image: url("../../../img/backapp.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 700px;

  .signin__wrapper {
    max-width: 400px;
    padding: 40px 32px 30px;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: linear-gradient(
      to bottom,
      rgba(20, 50, 93, 0.9),
      rgba(8, 22, 48, 0.9)
    );
    img {
        width: 100%;  
    }
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 45%);
    text-align: center;
    color: #fff;
    border-radius: 6px;
  }
  
  .signin__footer{
    justify-content:center;
    .btn__signin{
      background-color: #fb4226;
      color: #fff;
      font-weight: bold;
    }
  }
}
