.users__manager, .movie__manager, .calendar__manager{
    background-color: #3b434e;
    color: #f1f5f7;
    width: 100%;
    height: 100%;
    
    .sidebar__menu{
        li{
            a{
                color: #f1f5f7;
                font-size: 15px;
                i{
                    font-size: 20px;
                }
            }
            .nav__active{
                color: #fb4226;
            }
        }
    }
    .title__user__manager{
        color: #fff;
        font-size: 30px;
        font-weight: bold;
        line-height: 30px;
        
    }
    .add{
       
        .add__user{
            // position: absolute;
            // margin-right: 10%;
            background-color:#23b397 ;
            color: white;
            border-radius: 15px;
           
        }
        .add_film{
            background-color:#23b397 ;
            color: white;
            border-radius: 15px;
         
        }
    }
    
    .search__user{
        background-color:#434b56 ;
        color: #dee2e6;
        background-clip: padding-box;
        border: 1px solid #4b5460;
        border-radius: .2rem;
    }
    .user__manager__table{
        // width: 600px;
        // width: 1500px;
        // overflow: hidden;
        color: #adb5bd;
        thead{
            tr{
                th{
                    color: #dee2e6;
                    font-size: 15px;
                    padding: 0 5px;
                    text-align: left;
                    border-left: 1px solid #dee2e6;
                    border-right: 1px solid #dee2e6;
                    padding: 2px 7px;
                    margin: 0;
                }             
            }
        }
        tbody{
            tr{
                
                    td{
                        border-left: 1px solid #dee2e6;
                        border-right: 1px solid #dee2e6;
                        border-bottom: 1px solid #dee2e6;
                        padding: 2px 7px;
                        margin: 0;
                    }
                
            }
        }
        .btn__del{
            cursor: pointer;
            color: #fff;
            background-color: #e64212;
            border-radius: 5px;
            border: 1px solid #23b397;
            transition: all 0.3s;
            &:hover{
                background-color: #ad3613;
            }
        }
        .btn__edit{
            cursor: pointer;
            color: #fff;
            background-color: #23b397;
            border-radius: 5px;
            border: 1px solid #23b397;
            transition: all 0.3s;
            &:hover{
                background-color: #2a907c;
            }
        } 
    } 
    .user__pagination {
        .page-item a{
            color: #23b397;
           
        }
        .page-item.active .page-link{
            background-color: #23b397 ;
            color: #adb5a4;
        }
        .active__pagination{
            background-color: #23b397 ;
        }
    }
    .btn__add_calendar{
        background-color: #23b397;
        color: white;
        border-radius: 15px;
    }
}



