@import '../../Helper/mixin';
@import '../../Helper/variable';

#tix__footer {
	background-color: #222;
	padding-top: 20px;
	padding-bottom: 20px;
	color: #fff;
	// padding-top: 80px;
	width: 100%;
	margin-left: 0;
}
#tix__footer .container{
    width: 80%;
    margin-left: 10%;
}
.tix__title {
	color: #fb4226;
	font-size: 80%;
	margin-bottom: 15px;
	font-weight: bold;
}
.FAQ__Brand {
	a {
		font-size: 12px !important;
		font-size: 80%;
		font-weight: 700;
		white-space: nowrap;
		color: #fff;
		display: block;
		transition: all 0.2s;
		line-height: 2.3;
		cursor: pointer;
		text-decoration: none;
		&:hover {
			color: #fb4226;
		}
	}
}
.footer__logo {
	a {
		img {
			border-radius: 50%;
			height: 30px;
			width: 30px;
			margin-right: 15px;
			margin-bottom: 15px;
			background-color: #fff;
			-webkit-transition: 0.2s all;
			transition: 0.2s all;
			opacity: 1;
		}
		text-decoration: none;
		color: #000;
		cursor: pointer;
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
		background-color: transparent;
		&:hover {
			img {
				background-color: #fff9;
				opacity: 0.7;
			}
		}
	}
	.col-sm-12 {
		padding-left: 0;
	}
}
.mobile__social {
	a {
		text-decoration: none;
		cursor: pointer;
		img {
			background: 0 0;
			height: 30px;
			width: auto;
			margin: 5px;
		}
	}
}
.footer__hr {
	border-top: 1px solid #4a4a4a;
}
.footer__address {
	padding-bottom: 20px;
	img {
		width: 80px;
		height: auto;
		border-radius: 8px;
	}
	h5 {
		color: #fff;
		display: block;
		line-height: 1.8;
		font-size: 12px;
	}
	p {
		display: block;
		line-height: 1.8;
		color: #949494;
		font-size: 12px;
		font-weight: 400;
		margin: 0;
	}
}
.email {
	color: #fb4226;
	&:hover {
		text-decoration: none;
		cursor: pointer;
		color: #d82b22;
	}
}
.label {
	a {
		text-decoration: none;
		color: #000;
		cursor: pointer;
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
		img {
			width: 130px;
			float: right;
		}
	}
}


