       }
    }
    @include Responsive(xs){
       #tix__footer{
            padding-bottom: 180px;
            text-align: center;
            .footer__logo{
                display: none;
            }
            .tix__title{
                display: none;
            }
            .partner{
                margin-left: 11px;
                margin-top: 10px;
            }
            .mobile__social{
                display: none;
            }
            .label a img{
                float: left;
                margin-left: 13%;
                margin-top: 15px;
                margin-bottom: 25px;
            }
            .footer__address{
                .col-sm-1{
                    margin-bottom: 15px;
                }
            }
       }
    }


