.MaxWidth {
    max-width: 950px;
    margin: auto;
}

#app__free {
    #app_content {
        padding: 120px 0 80px 0;
        background-image: url("../../../img/backapp.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: auto;
        .app_left {
            color: #fff;
            font-weight: 700;
            font-size: xx-large;
        }
        .app_text__small {
            color: #fff;
            font-size: medium;
        }
        #app_btn {
            background-color: #fb4226;
            padding: 10px 20px;
            
            font-size: medium;
            font-weight: 700;
            border: none;
            border-radius: 4px;
            a{
                text-decoration: none;
                color: #fff;
            }
        }
        .app__text {
            color: #fff;
            padding-top: 10px;
            a{
                text-decoration: none;
                color: #fff;
                border-bottom: 1px solid #fff;
            }
        }
        .app___right {
            position: relative;
            padding: 0;
            .img__phone {
                max-width: 100%;
                padding: 0 28%;
                display: block;
                height: auto;
            }
            .app__slider {
                position: absolute;
                padding: 1.5% 29.3% 0 29.3%;
                width: 100%;
                top: 0;
                left: 0;
                .slick-list {
                    border-radius: 20px;
                }
                .slick-slide {
                    height: auto;
                }
                .slickComponent {
                    position: relative;
                }
                img {
                    width: 100%;
                }
            }
        }
    }
}
