#register{
    background-image: url('../../../img/bgregister.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 990px;
    .input-group-prepend{
        width: 80%;
        margin-left: 10%;
    }
    .form-control{
        width: 100%;;
    }
}
.register__wrappe{
    max-width: 400px;
    padding: 40px 32px 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: linear-gradient(
      to bottom,
      rgba(20, 50, 93, 0.9),
      rgba(8, 22, 48, 0.9)
    );
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 45%);
    text-align: center;
    color: #fff;
    border-radius: 6px;
    img{
        width: 100%;
    }
    .btn-register{
        background-color: #fb4226;
        color: #fff;
        font-weight: bold;
        // text-align: center;
        width: 80%;
      }
}